import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  ReferenceLine,
  Label,
  Brush,
} from "recharts"

import { useState, useRef, createRef, useEffect, useCallback } from "react"

import * as d3 from "d3"

import { geoVoronoi } from "d3-geo-voronoi"

import moment from "moment"

import chroma from "chroma-js"

import "moment/locale/fr"
import "moment/locale/en-gb"

export default function Chart({ water, cities }) {
  const french = navigator.language === "fr"
  if (french) {
    moment.locale("fr")
  }

  const d3Container = useRef(null)

  const displayEaster = document?.location?.hash !== ""
  const xAxis = "semaine"

  // todo autodetect multiple station
  const multipleStation = ["paris"]

  //
  let citiesName = cities
    .map(({ commune }) => commune.toLowerCase())
    .filter((name) => !multipleStation.includes(name))

  const waterValues = water?.map((week) =>
    Object.entries(week)
      // remove undefined values for a city this week
      .filter(([station, value]) => value !== "NA")
      // format the city name according to cities dataset
      .map(([station, value]) => {
        const city = cities
          ?.find(({ nom }) => station === nom)
          ?.commune.toLowerCase()
        // graphique name is the city name excepted if multiples stations per cities then we will
        // use the station name directly since he is allready prefixed by city name
        const name =
          !station.includes("National") &&
          station !== xAxis &&
          !multipleStation.includes(city)
            ? city
            : station.toLowerCase()
        if (multipleStation.includes(city)) {
          citiesName.push(station.toLowerCase())
        }
        return [name, value]
      })
      .reduce((accumulator, [name, value], index, all) => {
        // there is both National_54 and National_12 but sequential dealing before and after december 2023
        // when new 54 stations added
        name = name.includes("national") ? "national" : name
        return {
          ...accumulator,
          [name]:
            name === xAxis
              ? // date or water value by default
                moment(value, "YYYY-W")
                  .add(7, "d")
                  .format(french ? "YYYY-DD-MM" : "YYYY-MM-DD")
              : Number(value.split(",")[0]),
        }
      }, {})
  )
  // remove twins done to autodetect of multiple station per city then order alphabeticaly
  citiesName = [...new Set(citiesName)].sort()
  // set national in top position of the object
  if (citiesName.length > 0) citiesName.unshift("national")

  // generate palet of colors
  const colors = chroma
    .cubehelix()
    .start(0)
    .rotations(0.8)
    .lightness(0.5)
    .gamma(0.8)
    .hue(4)
    .scale()
    .colors(citiesName.length)

  const scale = chroma
    .scale([
      "#61afef", // blue
      "#98c379", // green
      "#e5c07b", // yellow
      "#db8b6b", // orange
      "#e06c75", // red
      "#ce4646", // black
    ])
    .colors(6)

  const levelToColor = (level) => {
    switch (true) {
      case level === 0:
        return scale[0]
      case level < 1000:
        return scale[1]
      case level < 2000:
        return scale[2]
      case level < 5000:
        return scale[3]
      case level < 8000:
        return scale[4] // red
      case level < 10000:
        return scale[5]
      default:
        return scale[5]
    }
  }

  /*

  filtering

  */

  const [showCity, setShowCity] = useState([])
  const [tempHover, setTempHover] = useState([])

  const filterWaterValue = (weeks, filters) =>
    weeks.map((week, index) =>
      Object.entries(week)
        // .filter(([name]) => filters.includes(name) || name === xAxis)
        .map(([city, value], index, all) => {
          if (filters.includes(city) || city === xAxis) {
            return [city, value]
          } else {
            return [city, 0]
          }
        })
        .reduce(
          (accumulator, [name, value]) => ({ ...accumulator, [name]: value }),
          {}
        )
    )
  // extract max of latestweek to display in the top of graph, more the biggest known cities
  const lastestWeek = waterValues.slice(-1)?.[0]

  let worst =
    lastestWeek &&
    Object.entries(lastestWeek).sort(
      ([nameA, valueA], [nameB, valueB]) => valueB - valueA
    )?.[1]?.[0]

  // initial filter is National, 3 biggest cities and the zone with the max value
  const initialFilter = [
    ...new Set([
      "national",
      "paris seine-centre",
      "marseille",
      "lyon",
      "nice",
      "toulouse",
      "nantes",
      "montpellier",
      "strasbourg",
      "bordeaux",
      "lille",
      "rennes",
      worst,
    ]),
  ]

  // build filter: initial filter or user choice
  let filter = showCity.length === 0 ? initialFilter : showCity

  let tempFilter = tempHover ? [...filter, ...tempHover] : filter

  const filtered = filterWaterValue(waterValues, tempFilter)

  const refs = useRef(citiesName.map((city, index) => createRef()))

  const easters = [
    {
      x: "2022-09-05",
      fr: "❝C'est un rhume❞",
      en: "❝It's mild❞",
      position: "insideBottom",
    },
    {
      x: "2022-10-17",
      fr: "❝un soubressaut qui sait❞",
      en: "❝just a bump who knows❞",
      position: "center",
    },
    {
      x: "2022-12-12",
      fr: "❝Personne n'aurait pu prévoir ça❞",
      en: "❝No one could foreseen this❞",
      position: "insideTop",
    },
    {
      x: "2023-02-13",
      fr: "❝C'est saisonnier❞",
      en: "❝It's seasonal❞",
      position: "insideBottom",
    },
    {
      x: "2023-05-01",
      fr: "❝5 mois de mémoire immunitaire, incroyable❞",
      en: "❝5 monthes of immune memory, incredible❞",
      position: "center",
    },
    {
      x: "2023-07-10",
      fr: "❝C'est terminé❞",
      en: "❝It's over❞",
      position: "insideBottom",
    },

    {
      x: "2023-12-18",
      fr: "❝Personne n'aurait pu prédire ça❞",
      en: "❝No one could have predicted this❞",
      position: "insideTop",
    },
    {
      x: "2023-09-04",
      fr: "❝Imprévisible cocktail des variants d'été❞",
      en: "❝Summer variant soup was unpredictable❞",
      position: "center",
    },
    {
      x: "2024-04-08",
      fr: "❝Immunité de masse❞",
      en: "❝Herd immunity❞",
      position: "insideBottom",
    },
    {
      x: "2024-06-24",
      fr: "❝Un variant en été, on l'a pas vu venir❞",
      en: "❝A new summer variant, we dont see it coming❞",
      position: "insideRight",
    },
  ]

  const handleMouseOut = useCallback(
    (event) => {
      event?.preventDefault?.()
      const city =
        (multipleStation.includes(event?.srcElement?.getAttribute("data-city"))
          ? event?.srcElement?.getAttribute("data-name")
          : event?.srcElement?.getAttribute("data-city")) || event.value
      setTempHover((old) => {
        return old.filter((name) => name !== city)
      })
    },
    [multipleStation]
  )
  const handleMouseOver = useCallback(
    (event) => {
      event?.preventDefault?.()
      const city =
        (multipleStation.includes(event?.srcElement?.getAttribute("data-city"))
          ? event?.srcElement?.getAttribute("data-name")
          : event?.srcElement?.getAttribute("data-city")) || event.value
      setTempHover((old) => {
        return [...old, city]
      })
    },
    [multipleStation]
  )
  useEffect(() => {
    const svg = d3.select(d3Container.current)

    const { height, width } = d3Container.current.getBoundingClientRect()

    const mercator = d3
      .geoMercator()
      .scale(1100) // zoom
      .center([2.5, 46.5]) // Centers the map on the center of France
      .translate([width / 2, height / 2])

    const geoPath = d3.geoPath().projection(mercator)
    const citiesCoordinates = cities
      .filter(
        (city) =>
          waterValues[waterValues.length - 1][city.nom.toLowerCase()] ||
          waterValues[waterValues.length - 1][city.commune.toLowerCase()]
      )
      .map(({ latitude, longitude, ...rest }) => ({
        type: "Feature",
        properties: {},
        info: rest,
        geometry: {
          type: "Point",
          coordinates: [
            parseFloat(longitude.replace(",", ".")),
            parseFloat(latitude.replace(",", ".")),
          ],
        },
      }))

    fetch("/covid-wasted-water-france/france-shape.json")
      .then((res) => res.json())
      .then((shape) => {
        const generatedVoronoi = geoVoronoi().polygons(citiesCoordinates)

        /* to use with react, d3 need to be included in the workflow, we cant simply  */
        const init = svg.select("clipPath").select("path")
        const clipPath = init._groups[0][0]
          ? init
          : svg.append("clipPath").attr("id", "clip").append("path")
        clipPath.datum(shape).attr("d", geoPath)

        const group = svg.select("g")._groups[0][0]
          ? svg.select("g")
          : svg.append("g")

        const update = group
          .attr("clip-path", "url(#clip)") // clip the rectangle
          .attr("fill", "transparent")
          .attr("pointer-events", "all")
          .selectAll("path")
          .data(generatedVoronoi.features)

        // there we add new elements who arent in the data yet with enter()
        update
          .enter()
          .append("path")
          .attr("fill", (cellPolygon, index, all) => {
            const currentWeek = waterValues[waterValues.length - 1]

            const level =
              currentWeek[
                cellPolygon.properties.site.info.commune.toLowerCase()
              ] ||
              currentWeek[cellPolygon.properties.site.info.nom.toLowerCase()]
            return levelToColor(level)
          })
          .attr("data-name", (cellPolygon) =>
            cellPolygon.properties.site.info.nom.toLowerCase()
          )
          .attr("data-city", (cellPolygon) =>
            cellPolygon.properties.site.info.commune.toLowerCase()
          )

          .attr("d", geoPath)

          .append("title")
          .text((cellPolygon, index) => {
            const currentWeek = waterValues[waterValues.length - 1]
            return `${cellPolygon.properties.site.info.commune.toLowerCase()} ${
              currentWeek[
                cellPolygon.properties.site.info.commune.toLowerCase()
              ] ||
              currentWeek[cellPolygon.properties.site.info.nom.toLowerCase()]
            }`
          })

        // there we got both newly added and old components
        update
          .classed("selected", (cellPolygon) => {
            const city = multipleStation.includes(
              cellPolygon.properties.site.info.commune
            )
              ? cellPolygon.properties.site.info.nom.toLowerCase()
              : cellPolygon.properties.site.info.commune.toLowerCase()

            return (
              (filter && filter?.includes(city)) ||
              (tempHover && tempHover?.includes(city))
            )
          })
          .on("mouseleave", handleMouseOut)
          .on("mouseenter", handleMouseOver)
          .on("click", function (event) {
            const city = multipleStation.includes(
              d3.select(this).attr("data-city")
            )
              ? d3.select(this).attr("data-name")
              : d3.select(this).attr("data-city")
            if (filter.includes(city)) {
              setShowCity(filter.filter((name) => name !== city))
              setTimeout(() => {
                setTempHover(tempHover.filter((name) => name !== city))
              }, 100)
            } else {
              setShowCity([...filter, city])
            }
          })

        update.exit().remove()
      })
    // return () => {
    //   d3.selectAll("[data-city]")
    //     .on("click", null)
    //     .on("mouseout", null)
    //     .on("mouseover", null)
    //     .on("mouseout", null)
    //     .on("mouseenter", null)
    //     .on("mouseover", null)
    // }
  }, [
    cities,
    waterValues,
    tempHover,
    filter,
    levelToColor,
    handleMouseOut,
    handleMouseOver,
    multipleStation,
  ])

  return (
    <div className="dashboard">
      <section id="cartography" className="carto">
        <header>
          <p>
            <a href="#graph">{french ? "Séries temporelles" : "Time series"}</a>
            {french ? " et " : " and "}
            <a href="#cartography">
              {french ? "Visualisation cartographique" : "Map Visualization"}
            </a>
            {french
              ? " de la concentration de SARS-CoV-2 en France."
              : " of the SARS-CoV-2 concentration in France."}
            <br />
            {french ? "A jour du:" : "Updated:"}
            <time
              className="date"
              dateTime={moment(
                waterValues[waterValues.length - 1][xAxis],
                french ? "YYYY-DD-MM" : "YYYY-MM-DD"
              ).format("YYYY-MM-DD")}
            >
              {moment(
                waterValues[waterValues.length - 1][xAxis],
                french ? "YYYY-DD-MM" : "YYYY-MM-DD"
              ).format("DD MMMM YYYY")}
            </time>
            <br />
            {french
              ? "🛈 Sélectionez le détails des villes en cliquant sur la carte, le graph, ou la légende."
              : "🛈 Select cities details by clicking on the map, the graph, or the legend."}
            <br />
            {french ? "Preset" : "Preset"}:
            <button
              className="reset"
              data={{}}
              onClick={(e) => {
                setShowCity(["national"])
              }}
            >
              {french ? "La moyenne nationale" : "National average"}
            </button>{" "}
            <button
              className="reset"
              data={{}}
              onClick={(e) => {
                setShowCity(citiesName)
              }}
            >
              {french ? "Toutes les villes" : "All cities"}
            </button>{" "}
            <button
              className="reset"
              data={{}}
              onClick={(e) => {
                setShowCity(initialFilter)
              }}
            >
              {french ? "grandes villes" : "Bigest cities"}
            </button>{" "}
          </p>
        </header>
        {
          <h3 className="counter">
            <span className={"city "}>
              {tempHover?.[0] ||
                (french ? "Moyenne nationale" : "National Average")}
            </span>
            <span
              className={"concentration "}
              style={{
                background: levelToColor(
                  tempHover.length > 0
                    ? waterValues[waterValues.length - 1][tempHover?.[0]]
                    : waterValues[waterValues.length - 1]["national"]
                ),
              }}
            >
              <span className="">
                {tempHover.length > 0
                  ? waterValues[waterValues.length - 1][tempHover?.[0]]
                  : waterValues[waterValues.length - 1]["national"]}
              </span>
            </span>
          </h3>
        }
        <svg className="cartosvg" ref={d3Container}>
          <defs>
            <filter id="glow">
              <feGaussianBlur stdDeviation="1" />
            </filter>
            <filter id="glowlarge">
              <feGaussianBlur stdDeviation="2" />
            </filter>
            <filter id="erode">
              <feMorphology operator="erode" radius="1" />
            </filter>
          </defs>
        </svg>
        {scale.map((color, index) => (
          <span key={index} style={{}}></span>
        ))}
      </section>

      <section id="graph" className="graph">
        <ResponsiveContainer
          width="90%"
          height={"100%"} // will set max-height for his subwrapper
          minHeight={600}
          maxHeight={"10%"}
          style={{ resize: "both", overflow: "visible", height: "100%" }}
        >
          <LineChart
            data={filtered}
            margin={{
              right: 30,
              left: 20,
            }}
          >
            {citiesName.map((city, index, all) => (
              <Line
                type="monotone"
                connectNulls
                dot={false}
                dataKey={city}
                className={!tempFilter?.includes(city) && "desactivated"}
                key={city}
                ref={refs.current[index]}
                stroke={
                  tempFilter?.includes(city)
                    ? colors[index]
                    : "rgb(122, 122, 122)"
                }
                strokeWidth={tempHover && tempHover.includes(city) ? 10 : 2}
                strokeDasharray={city === "national" ? "4 4" : "0 0"}
                name={city}
                id={city}
                isAnimationActive={true}
                animationDuration={100}
              ></Line>
            ))}

            <CartesianGrid strokeDasharray="3 3" />
            <XAxis tickMargin={5} dataKey={xAxis} />
            <YAxis
              label={{
                value: french
                  ? "Concentration virale cg/L"
                  : "Viral concentration cg/L",
                angle: -90,
                position: "left",
                textAnchor: "middle",
              }}
            ></YAxis>
            <Brush
              // TODO initial state passed by setTempHover
              travellerWidth={15}
              dataKey={(a) => {
                return a[xAxis]
              }}
            />
            <Legend
              width={97}
              align="center"
              wrapperStyle={{
                width: "98%",
              }}
              onMouseOver={handleMouseOver}
              onMouseOut={handleMouseOut}
              onClick={(e) => {
                if (filter.includes(e.value)) {
                  setShowCity(filter.filter((name) => name !== e.value))
                } else {
                  setShowCity([...filter, e.value])
                }
                setTimeout(() => {
                  setTempHover(tempHover.filter((name) => name !== e.value))
                }, 500)
              }}
            />
            <Tooltip
              offset={40}
              contentStyle={{
                background: "rgb(33, 37, 43)",
                textAlign: "left",
              }}
              content={({ active, payload, label }) =>
                active &&
                payload &&
                label && (
                  <div className="recharts-default-tooltip">
                    <p className="recharts-tooltip-label">
                      {moment(
                        label,
                        french ? "YYYY-DD-MM" : "YYYY-MM-DD"
                      ).format("DD MMMM YYYY")}
                    </p>
                    <ul className="recharts-tooltip-item-list">
                      {payload
                        .sort((first, second) => second.value - first.value)
                        .map(
                          (line, index) =>
                            line.value > 0 && (
                              <li key={index} className="recharts-tooltip-item">
                                <span
                                  className="recharts-tooltip-item-name"
                                  style={{
                                    color: line.color,
                                  }}
                                >
                                  {line.name}
                                </span>{" "}
                                <span className="recharts-tooltip-item-value">
                                  {line.value}
                                </span>
                              </li>
                            )
                        )}
                    </ul>
                  </div>
                )
              }
            />
            {displayEaster &&
              easters.map(({ x, position, fr, en }, index, all) => (
                <ReferenceLine
                  key={index}
                  x={moment(x, "YYYY-MM-DD").format(
                    french ? "YYYY-DD-MM" : "YYYY-MM-DD"
                  )}
                  stroke="transparent"
                  label={
                    <Label
                      width={120}
                      offset={
                        (position === "insideTop" && 15) ||
                        (position === "insideBottom" && 70) ||
                        0
                      }
                      value={french ? fr : en}
                      position={position}
                    />
                  }
                />
              ))}
          </LineChart>
        </ResponsiveContainer>
      </section>
    </div>
  )
}
